body .btn-bg {
  background-color: #48a955;
  color: #FFF;
  text-shadow: 0px 2px 6px #00000075;
  box-shadow: 0 2px 10px #00000020;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  border: 2px solid #48a955 !important;
}
body .btn-bg:hover, body .btn-bg:focus {
  background-color: #358835;
  box-shadow: 0 2px 10px #00000050;
  border: 2px solid #358835 !important;
  color: #FFF;
}
body .light-box-shadow {
  box-shadow: 0 2px 10px #00000020;
}
body .dark-box-shadow {
  box-shadow: 0 2px 10px #00000050;
}
body .animated-dropdown {
  animation-duration: 0.3s;
  -webkit-animation-duration: 0.3s;
  animation-fill-mode: both;
  -webkit-animation-fill-mode: both;
  top: 40px;
}
@keyframes slideIn {
  0% {
    transform: translateY(1rem);
    opacity: 0;
  }
  100% {
    transform: translateY(0rem);
    opacity: 1;
  }
  0% {
    transform: translateY(1rem);
    opacity: 0;
  }
}
@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: transform;
    -webkit-opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    -webkit-opacity: 1;
  }
  0% {
    -webkit-transform: translateY(1rem);
    -webkit-opacity: 0;
  }
}
body .slideIn {
  -webkit-animation-name: slideIn;
  animation-name: slideIn;
}

